<template>
  <el-container class="main-container">
    <!-- 头部区域 -->
    <el-header>
      <!-- 左侧的 logo -->
      <img src="../../assets/images/logo.png" alt="" />
      <!-- 右侧的菜单 -->
      <el-menu
        class="el-menu-top"
        mode="horizontal"
        background-color="#23262E"
        text-color="#fff"
        active-text-color="#409EFF"
      >
        <el-submenu index="1">
          <template slot="title">
            <!-- 头像 -->
            <img
              v-if="userInfo.user_pic"
              :src="userInfo.user_pic"
              alt=""
              class="avatar"
            />
            <img
              v-else
              src="../../assets/images/jiaran.jpg"
              alt=""
              class="avatar"
            />
            <span>个人中心</span>
          </template>
          <el-menu-item index="1-1" @click="$router.push('/user-info')"
            ><i class="el-icon-s-operation"></i>基本资料</el-menu-item
          >
          <el-menu-item index="1-2" @click="$router.push('/user-Avatar')"
            ><i class="el-icon-camera"></i>更换头像</el-menu-item
          >
          <el-menu-item index="1-3" @click="$router.push('/user-pwd')"
            ><i class="el-icon-key"></i>重置密码</el-menu-item
          >
        </el-submenu>
        <el-menu-item index="2" @click="logout"
          ><i class="el-icon-switch-button"></i>退出</el-menu-item
        >
      </el-menu>
    </el-header>
    <el-container>
      <!-- 侧边栏区域 -->
      <el-aside width="200px">
        <div class="user-box">
          <img v-if="userInfo.user_pic" :src="userInfo.user_pic" alt="" />
          <img v-else src="../../assets/images/jiaran.jpg" alt="" />
          <span>欢迎 {{ userInfo.nickname || userInfo.username }}</span>
        </div>
        <el-menu
          :default-active="$route.path"
          class="el-menu-vertical-demo"
          background-color="#23262E"
          text-color="#fff"
          active-text-color="#409EFF"
          unique-opened
          router
        >
          <template v-for="item in menu">
            <!-- v-for 不能在template标签上添加: key 但可以在遍历的子项添加 -->
            <!-- 不包含子菜单的“一级菜单” -->
            <el-menu-item
              :key="item.indexPath"
              :index="item.indexPath"
              v-if="!item.children"
              ><i :class="item.icon"></i>{{ item.title }}</el-menu-item
            >
            <!-- 包含子菜单的“一级菜单” -->
            <el-submenu v-else :key="item.indexPath" :index="item.indexPath">
              <template slot="title">
                <i :class="item.icon"></i>
                <span>{{ item.title }}</span>
              </template>
              <el-menu-item
                :index="subItem.indexPath"
                v-for="subItem in item.children"
                :key="subItem.indexPath"
                ><i :class="subItem.icon"></i>{{ subItem.title }}</el-menu-item
              >
            </el-submenu></template
          >
        </el-menu>
      </el-aside>
      <el-container>
        <!-- 页面主体区域 -->
        <el-main>
          <router-view></router-view>
        </el-main>
        <!-- 底部 footer 区域 -->
        <el-footer
          ><p>&copy; 咸鱼出品, 必属精品</p>
          &emsp;&emsp;
          <p>你所热爱的, 就是你的生活</p>
        </el-footer>
      </el-container>
    </el-container>
  </el-container>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'Main',
  created () {
    if (!this.token) return this.$router.push('/login')
    this.$store.dispatch('getUserInfo')
    this.getAsideList()
  },
  data () {
    return {
      menu: []
    }
  },
  methods: {
    logout () {
      // 退出登录功能
      this.$confirm('确认退出?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          // 1. 清空 token
          this.$store.commit('updateToken', '') // commit:调用mutations里的方法, (函数名,传参)
          // 2. 跳转到登录页面
          this.$router.push('/login')
        })
        .catch((err) => {
          console.log(err)
        })
    },
    // 获取左侧功能列表
    async getAsideList () {
      const { data: res } = await this.$http({
        method: 'get',
        url: '/my/menus'
      })
      console.log(res)
      this.menu = res.data
    }
  },
  computed: {
    ...mapState(['userInfo', 'token'])
  }
}
</script>

<style lang="less" scoped>
.main-container {
  height: 100%;
  .el-header,
  .el-aside {
    background-color: #23262e;
    // 左侧边栏用户信息区域
    .user-box {
      height: 70px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-top: 1px solid #000;
      border-bottom: 1px solid #000;
      user-select: none;
      img {
        width: 35px;
        height: 35px;
        border-radius: 50%;
        background-color: #fff;
        margin-right: 15px;
        object-fit: cover;
      }
      span {
        color: white;
        font-size: 12px;
      }
    }
    // 侧边栏菜单的样式
    .el-submenu,
    .el-menu-item {
      width: 200px;
      user-select: none;
    }
  }
  .el-header {
    padding: 0;
    display: flex;
    justify-content: space-between;
  }
  .el-main {
    overflow-y: scroll;
    height: 0;
    background-color: #f2f2f2;
  }
  .el-footer {
    background-color: #eee;
    font-size: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.avatar {
  border-radius: 50%;
  width: 35px;
  height: 35px;
  background-color: #fff;
  margin-right: 10px;
  object-fit: cover;
}
</style>
